// Import dependencies
import { AutomatorLogItem } from '../_base';
import { unsafeCSS, css, html } from 'lit';
import { customElement } from 'lit/decorators.js';
import { sprintf } from '@automator/utilities';

// Import style for web component
const styles = require( './web-component-style.scss' );

// Get translatable strings
const strings = UncannyAutomatorBackend.logs.components.logItemTrigger.i18n;

// eslint-disable-next-line require-jsdoc
@customElement( 'uap-log-item-trigger' )
export class AutomatorLogTrigger extends AutomatorLogItem {
	// Define style
	static styles = css`${ unsafeCSS( styles ) }`;

	/**
	 * Creates the object passed to the sidebar
	 */
	get sidebarContent() {
		return {
			title: strings.sidebarTitle,
			summary: this.sidebarSummary,
			runs: this.sidebarRuns
		}
	}

	/**
	 * Creates the summary for the sidebar
	 */
	get sidebarSummary() {
		// Summary items
		const items = [];

		// Add status
		items.push( {
			label: strings.summary.status,
			value: html`
				<uap-log-status status-id="${ this.statusID }"></uap-log-status>
			`
		} );

		// Add start and completion date
		// If it's a range, add "Start date" and "End date". If there is no end date, then just add "Date"
		const dateIsRange = this.startDate !== this.endDate;

		items.push( {
			label: dateIsRange ? strings.summary.startDate : strings.summary.date,
			value: this.startDate
		} );

		if ( dateIsRange ) {
			// Add "End date"
			items.push( {
				label: strings.summary.endDate,
				value: this.endDate
			} );
		}

		// Add the runs, but only if there is more than one
		if ( this.runs.length > 1 ) {
			items.push( {
				label: strings.summary.runs,
				value: this.runs.length
			} );
		}

		return {
			title: html`
				<uap-log-integration-icon integration-id="${ this.integrationCode }"></uap-log-integration-icon>
				<uap-log-sentence .sentence="${ this.titleHTML }"></uap-log-sentence>
			`,
			items: items
		}
	}

	/**
	 * Creates the array for the sidebar to render the trigger runs
	 */
	get sidebarRuns() {
		const runs = [];

		// Get the number of runs
		const numberOfRuns = this.runs.length;

		if ( numberOfRuns === 0 ) {
			return;
		}

		this.runs.forEach( ( run, index ) => {
			// Create a summary of each run
			const runSummary = [];

			// Add the run status
			runSummary.push( {
				label: strings.summary.status,
				value: html`
					<uap-log-status status-id="${ run.status_id }"></uap-log-status>
				`
			} );

			// Add the date
			runSummary.push( {
				label: strings.summary.date,
				value: run.date
			} );

			runs.push( {
				title: html`
					<uap-log-status status-id="${ run.status_id }" hide-label></uap-log-status>
					${ sprintf( strings.runNumber, `${ ( index + 1 ) }/${ numberOfRuns }` ) }
				`,
				summary: runSummary,
				properties: [ ...this.fieldsForSidebar, ...run.properties ]
			} );
		} );

		return runs;
	}

	/**
	 * The message shown when the action data is missing
	 * 
	 * @returns {String}
	 */
	get dataMissingErrorMessage() {
		return strings.missingItem;
	}
}
