// Import dependencies
import { unsafeCSS, css, html, LitElement } from 'lit';
import { customElement, property } from 'lit/decorators.js';

// Import style for web component
const styles = require( './web-component-style.scss' );

// Get translatable strings
const strings = UncannyAutomatorBackend.logs.components.log.i18n.actions;

// eslint-disable-next-line require-jsdoc
@customElement( 'uap-log-header-action-close-dialog' )
export class AutomatorLogHeaderActionCloseDialog extends LitElement {
    // Define style
    static styles = css`${ unsafeCSS( styles ) }`;

    // eslint-disable-next-line require-jsdoc
    render() {
        return html`
            <uo-tooltip>
                <span class="tooltip-nowrap">${ strings.closeLogDetails }</span>

                <uo-button
                    slot="target"
                    color="transparent"
                    size="small"

                    @click=${ this.requestCloseDialog }
                >
                    <uo-icon id="times" size="16"></uo-icon>
                </uo-button>
            </uo-tooltip>
        `;
    }

    /**
     * Requests the dialog to close itself 
     */
    requestCloseDialog() {
        this.dispatchEvent( 
            new CustomEvent(
                'uap-log-request-dialog-close',
                {
                    bubbles: true,
                    composed: true
                }
            ) 
        );
    }
}