// Import dependencies
import { unsafeCSS, css, html, LitElement } from 'lit';
import { property } from 'lit/decorators.js';
import { isEmpty, isDefined, sprintf } from '@automator/utilities';

// Get translatable strings
const strings = UncannyAutomatorBackend.logs.components.logItemItem.i18n;

// eslint-disable-next-line require-jsdoc
const LogItemMixin = ( superClass ) => class extends superClass {
	// Properties
	@property({ type: Number, attribute: 'item-id' }) id;
	@property({ type: String, attribute: 'item-code' }) code;

	@property({ type: String, attribute: 'integration-code' }) integrationCode;

	@property({ type: String, attribute: 'status-id' }) statusID;

	@property({ type: String, attribute: 'start-date' }) startDate;
	@property({ type: String, attribute: 'end-date' }) endDate = this.startDate;

	@property({ type: Boolean, attribute: 'is-open' }) isOpen = false;
	
	// Check if the data is coming from an old version of Automator
	@property({ type: Boolean, attribute: 'from-legacy-log' }) fromLegacyLog = false;
	// This property is used to indicate to the web component that it shouldn't try to retrieve data from the server
	// and that it should use the data provided when it was created
	@property({ type: Boolean, attribute: 'import-mode' }) isImportMode = false;

	@property({ type: String, attribute: false }) titleHTML = '';
	@property({ type: Array, attribute: false }) fields = [];
	@property({ type: Array, attribute: false }) runs = [];

	// eslint-disable-next-line require-jsdoc
	render() {
		// Check if the data is missing
		if ( this.isDataMissing ) {
			return this._templateMissingData;
		}

		return html`
			<div 
				class="item item--${ this.statusID }"
			>
				<div class="item__icon">
					<uap-log-integration-icon size="16" integration-id="${ this.integrationCode }"></uap-log-integration-icon>
				</div>

				<div class="item__sentence">
					<uap-log-sentence .sentence="${ this.titleHTML }"></uap-log-sentence>
				</div>

				<div class="item__details">
					${ this._templateDate }
					${ this._templateRuns }
					${ this._templateStatus }
				</div>

				${ this._templateOpenSidebarButton }
			</div>
		`;
	}

	/**
	 * Keeps the sidebar up to date
	 * Called before update() to compute values needed during the update.
	 * We'll use this method to keep the sidebar up to date when one the reactive properties of the action change
	 */
	willUpdate( changedProperties ) {
		// Check if the property that changed was .isOpen
		// If that's the case, WE DON'T NEED TO DO THIS HERE AGAIN
		// When the property .isOpen changes, `.requestSidebar()` is invoked, which
		// is already sending the request to the main component to update the sidebar data
		if ( changedProperties.has( 'isOpen' ) ) {
			return;
		}

		// Check if it's open
		if ( ! this.isOpen ) {
			return;
		}

		// Request sidebar update
		this.updateSidebar();
	}

	/**
	 * Opens the sidebar to show more data about this item
	 */
	requestSidebar() {
		if ( this.isOpen ) {
			// Close it
			this.dispatchEvent( 
				new CustomEvent( 'uap-log-request-sidebar', {
					bubbles: true,
					composed: true,
					detail: {
						action: 'close',
					}
				} )
			);

			return;
		}

		this.updateSidebar();
	}

	/**
	 * Updates the sidebar data
	 */
	updateSidebar() {
		this.dispatchEvent( 
			new CustomEvent( 'uap-log-request-sidebar', {
				bubbles: true,
				composed: true,
				detail: {
					action: 'open',
					sourceID: `item-${ this.id }`,
					content: this.sidebarContent
				}
			} ) 
		);
	}

	/**
	 * Renders the template used when the basic data of the trigger/action is missing
	 * 
	 * @returns {TemplateResult}
	 */
	get _templateMissingData() {
		return html`
			<div class="item item--missing">
				<div class="item__icon">
					<uap-log-integration-icon size="16"></uap-log-integration-icon>
				</div>

				<div class="item__plain-message">
					${ this.dataMissingErrorMessage }
				</div>
			</div>
		`;
	}

	/**
	 * Returns the template of the button that requests the sidebar
	 * 
	 * @returns {TemplateResult}
	 */
	get _templateOpenSidebarButton() {
		// Check if we shouldn't add the button at all
		if ( this.statusID === 'not-completed' ) {
			return;
		}

		// Skipped actions that have ran before Automator 5.0 contain the conditions message inside the actions notes
		// So we have to show the sidebar in that case even though there are no fields
		if ( this.statusID === 'skipped' && ! this.fromLegacyLog ) {
			return;
		}

		return html`
			<uo-tooltip class="item__more-info">
				${ ! this.isOpen ? strings.openInSidebar : strings.closeSidebar }

				<uo-button 
					slot="target"
					color="transparent" 
					@click=${ this.requestSidebar }
				>
					<uo-icon id="${ this.isOpen ? 'angle-left' : 'angle-right' }"></uo-icon>
				</uo-button>
			</uo-tooltip>
			
		`;
	}

	/**
	 * Returns the HTML of the section that shows the start and end date
	 * 
	 * @return {TemplateResult} The date
	 */
	get _templateDate() {
		// Check if the item hasn't run yet
		if ( 
			isEmpty( this.startDate ) || 
			[ 'skipped' ].includes( this.statusID )
		) {
			return;
		}

		// Check if the date is a range
		const dateIsRange = ! isEmpty( this.endDate ) && this.startDate !== this.endDate;

		return html`
			<div class="item__detail">
				<uo-icon class="item__detail-icon" id="clock"></uo-icon>

				${ 
					dateIsRange ? 
					sprintf( strings.dateRange, this.startDate, this.endDate ) :
					this.startDate
				}
			</div>
		`;
	}

	/**
	 * Returns the HTML of the section that shows the number of runs
	 * This will return HTML only if there were more than 1 runs.
	 * 
	 * @return {TemplateResult} The number of runs
	 */
	get _templateRuns() {
		// Check if there if there is more than one, otherwise don't return anything
		if ( this.runs.length <= 1 ) {
			return html``;
		}

		return html`
			<div class="item__detail">
				<uo-icon class="item__detail-icon" id="hashtag"></uo-icon>

				${ sprintf( strings.runs, this.runs.length ) }
			</div>
		`;
	}

	/**
	 * Returns the HTML of the section that shows the status
	 * 
	 * @return {TemplateResult} The status of this item
	 */
	get _templateStatus() {
		return html`
			<div class="item__detail">
				<uap-log-status status-id="${ this.statusID }"></uap-log-status>
			</div>
		`;
	}

	/**
	 * Converts fields into properties for the sidebar
	 * 
	 * @returns {Array} where each element is a field of the trigger or action, prepared for the sidebar
	 */
	get fieldsForSidebar() {
		// Prepare fields for sidebar
		const fieldsSidebar = [];

		this.fields.forEach( field => {
			// Get the value of the field that'll show in the sidebar
			// Actions will have a `parsed` property, which will contain the value of the tokens 
			// instead of the token IDs
			let fieldValue = isDefined( field.value.parsed ) ? field.value.parsed : field.value.raw;

			// Selects will also contain a `readable` property, which will contain the text of the option
			if ( ! isEmpty( field.value.readable ) ) {
				fieldValue = `${ fieldValue } (${ field.value.readable })`; 
			}

			fieldsSidebar.push( {
				type:  field.type,
				label: field.label,
				value: fieldValue,
				attributes: ! isEmpty( field.attributes ) ? field.attributes : {}
			} );
		} );

		return fieldsSidebar;
	}

	/**
	 * Checks whether this item was removed and the basic data is missing
	 * 
	 * @returns {Boolean}
	 */
	get isDataMissing() {
		return this.integrationCode === 'INTEGRATION_CODE_NOT_FOUND' || [ 'TRIGGER_CODE_NOT_FOUND', 'ACTION_CODE_NOT_FOUND' ].includes( this.code );
	}
}

export const AutomatorLogItem = LogItemMixin( LitElement );
