// Import dependencies
import { unsafeCSS, css, html, LitElement, render } from 'lit';
import { customElement, property } from 'lit/decorators.js';
import { createRef, ref } from 'lit/directives/ref.js';
import { isDefined } from '@automator/utilities';

// Import style for web component
const styles = require( './web-component-style.scss' );

// Get translatable strings
const strings = UncannyAutomatorBackend.logs.components.logDialogButton.i18n;

// eslint-disable-next-line require-jsdoc
@customElement( 'uap-log-dialog-button' )
export class AutomatorLogDialogButton extends LitElement {
	// Define style
	static styles = css`${ unsafeCSS( styles ) }`;

	// Properties
	@property({ type: Number, attribute: 'log-id' }) logID;
	@property({ type: Number, attribute: 'recipe-id' }) recipeID;
	@property({ type: Number, attribute: 'run-number' }) runNumber;

	// Create a reference to the dialog that doesn't exist yet
	$dialog = createRef();

	// eslint-disable-next-line require-jsdoc
	render() {
		return html`
			<div class="button">
				<uo-tooltip>
					${ strings.viewDetails }

					<uo-button
						color="transparent"
						size="small"
						slot="target"

						@click=${ this.createDialog }
					>
						<uo-icon id="info-circle"></uo-icon>
					</uo-button>
				</uo-tooltip>
			</div>
		`;
	}

	/**
	 * Creates the dialog with the log data
	 * It also resizes the dialog when the child component shows or hides the sidebar
	 */
	createDialog() {
		// Create the node that we'll use to render the dialog
		// This node will be added as a direct child of the body node.
		// We can't add it inside the component created on this class because of z-index issues
		const $wrapper = document.createElement( 'div' );
		document.body.append( $wrapper );

		render(
			html`
				<uo-dialog
					size="large"
					open

					no-header
					no-footer
					no-padding

					${ ref( this.$dialog ) }

					@uap-log-request-dialog-close=${ ( event ) => {
						// Check if the dialog reference exists
						if ( ! isDefined( this.$dialog.value ) ) {
							return;
						}

						this.$dialog.value.close();
					} }
				>
					<uap-log
						log-id="${ this.logID }"
						recipe-id="${ this.recipeID }"
						run-number="${ this.runNumber }"

						@uap-log-sidebar-visibility-changed=${ ( event ) => {
							// Check if the dialog reference exists
							if ( ! isDefined( this.$dialog.value ) ) {
								return;
							}

							// If the sidebar is open, make the dialog bigger
							if ( event.detail.isOpen ) {
								this.$dialog.value.setAttribute( 'size', 'xlarge' );
								return;
							}

							// Otherwise, make it smaller
							this.$dialog.value.setAttribute( 'size', 'large' );
						} }
					></uap-log>
				</uo-dialog>
			`,
			$wrapper
		);
	}
}